body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fullpage {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h1 {
    font-size: 50px !important;
}
.first {
    color: aliceblue;
}
.icons-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.myimg {
    width: 200px;
    height: 200px;
    border-radius: 100%;
}
.icon {
    margin: 10px;
    margin-top: 10px;
}

.imgdown {
    max-width: 40px;
    width: 40px !important;
    height: 40px !important;
    margin-top: 10px;
}
.imgdown:hover {
    -webkit-transform: scale(
        1.6
    );
            transform: scale(
        1.6
    ); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}
.title {
    cursor: pointer;
}

.titleabout {
    margin-right: 800px;
}
h1 {
    font-size: 70px;
}

p {
    padding-right: 55px !important;
    padding-left: 55px !important;
    color: aliceblue !important;
}
.imgdown1 {
    max-width: 40px;
    width: 40px !important;
    height: 40px !important;
    margin-top: 10px;
}
.imgdown1:hover {
    -webkit-transform: scale(
        1.2
    );
            transform: scale(
        1.2
    ); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}
.moreabout {
    margin-right: 150px;
    margin-left: 150px;
}

.card {
    cursor: pointer;
}

.titleskill {
    margin-right: 800px;
}
h1 {
    font-size: 75px !important;
    color: aliceblue;
}
.cards-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
img {
    width: 110px;
    height: 110px;
}
.card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 200px;
    width: 120px;
    background-color: azure;
    margin: 10px;

    box-shadow: 0 1px 3px rgb(39, 36, 36), 0 1px 2px rgb(27, 26, 26);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.card:hover {
    box-shadow: 0 14px 28px black, 0 10px 10px rgb(52, 52, 65);
}

.image-wrapper {
    width: 110px;
    height: 90px;
    text-align: center;
    padding: 5px;
}

.skill-title-wrapper {
    padding: 5px;
    height: 100px;
    width: 110px;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: flex-end;
}
h3 {
    font-size: 28px !important;
}

html,
body {
    height: 100%;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

h3 {
    font-size: 1rem;
}

.a1 {
    font-size: 20px;
    color: aliceblue !important;
    float: left;
    margin-left: 15px;
    cursor: pointer;
    text-decoration: none;
}
.a1:visited {
    text-decoration: none;
}
.a1 :hover {
    font-size: 30px;
    text-decoration: underline;
}

.a1:active {
    text-decoration: underline;
}

.a2 {
    color: aliceblue !important;
    font-size: 30px;
    float: right;
    margin-right: 25px;
    cursor: pointer;
}
.a3 {
    color: aliceblue !important;
    font-size: 30px;
    float: right;
    margin-right: 25px;
    cursor: pointer;
}

.scroll {
    position: fixed;
    bottom: 10px;
    margin-left: 30px;
}
.scroll:hover {
    -webkit-transform: scale(
        1
    );
            transform: scale(
        1
    ); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

